  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: right;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 80px;
    font-weight: normal;
  }
  
  .nav-links {
    color: white;
    opacity: 1;
    text-decoration: none;
    padding: 0.5rem 1rem;
    font-weight: normal;
  }
  
  .nav-links:hover {
    color: #9b8884;
    /* background-color: #cfc3ba; */
    border-radius: 4px;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #e1d6d6;
      left: 0;
      opacity: 0.75;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: left;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      /* background-color: #cfc3ba; */
      border-radius: 0;
    }
  
    /* .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    } */
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #e1d6d6;
      text-decoration: none;
      color: white;
      opacity: 1;
      font-size: 1.5rem;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #e1d6d6;
      transition: 250ms;
    }
  }
  
